.friends_header{
      display: flex;
      height: 34px;
      align-items: center;
      gap: 2rem;
      justify-content: flex-start;
}

.friends_headr__title{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding-left: 1rem;
      height: 34px;
      width: 142px;
      background: var(--theme);
      gap: .5rem;
      border-radius: 0 999px 999px 0;
}

.friends_headr__title *{
      font-size: 1.6rem;
      color: var(--light);
}

.friends_headr__title h4{
      text-transform: uppercase;
      font-size: 1rem;
}

.friends_header__count{
      width: 34px;
      height: 34px;
      background: var(--theme);
      border-radius: 50%;
      color: var(--light);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1.4rem;
}

.friends_slider{
      padding: 3rem 5rem;
      position: relative;
}

.friend_card,
.friend_card a{
      width: 110px;
      height: 110px;
      background: var(--gradient-45);
      padding: .5rem;
      border-radius: .4rem;
      position: relative;
      overflow: hidden;
}

.friend_card{
      padding: 0;
}

.friend_card a img{
      width: 100px;
      height: 100px;
      border-radius: .4rem;
}

.friend_card__name{
      position: absolute;
      width: 100%;
      height: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      background: var(--gradient-45);
      left: 0;
      top: 100%;
      color: var(--light);
      box-shadow: 0 0 10px rgba(0,0,0,.3);
      border-radius: .5rem .5rem 0 0;
      transition: .4s;
}

.friend_card:hover .friend_card__name{
      top: 50%;
}

/** slick **/

.slick-slide{
      margin-left: 15px;
}

.slick-list{
      height: 120px;
      margin: 0 15px;
}

.slick-slider{
      position: relative;
}

.slick-arrow{
      width: 36px;
      height: 36px;
      position: absolute;
      top: 50%;
      border-radius: 50%;
}

.slick-arrow:before{
      width: 10px;
      height: 10px;
      left: 50%;
      top: 50%;
      border-top: solid 2px var(--theme);
      border-left: solid 2px var(--theme);
      color: transparent;
}

.slick-arrow.slick-prev:before{
      transform: translate(-50%, -50%) rotate(-45deg);
}

.slick-arrow.slick-next:before{
      transform: translate(-50%, -50%) rotate(135deg);
}


.slick-arrow.slick-prev{
      left: -3rem;
}

.slick-arrow.slick-next{
      right: -3rem;
}