.content{
      width: 732px;
      height: auto;
      margin: 0 auto;
      margin-top: 10rem;
}

.heart_top{
      width: 100%;
      height: 34px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 3rem;
}

.heart_top h3{
      width: 172px;
      height: 34px;
      text-align: center;
      line-height: 34px;
      background: var(--gradient-45);
      border-radius: 999px;
      text-transform: uppercase;
      flex-shrink: 0;
      font-size: 1.2rem;
}

.header_top__title{
      width: 410px;
      height: 34px;
      line-height: 34px;
      padding: 0 2rem;
      background: var(--dark-100);
      border-radius: 999px;
}

.header_top__title span{
      font-size: 1.4rem;
      font-weight: 700;
}

.dots_header{
      width: 86px;
      height: 22px;
      position: relative;
}

.dots_header:before{
      width: 22px;
      height: 22px;
      background: var(--light);
      top: 0;
      left: 0;
      box-shadow: 32px 0 var(--light), 64px 0 var(--light);
      border-radius: 50%;
      opacity: .1;
}

.main_content{
      margin-top: 6rem;
      display: grid;
      grid-template-columns: 490px 210px;
      gap: 3.2rem;
}

/** section main **/

.main_content__group{
      width: 100%;
      height: 660px;
      position: relative;
      z-index: 2;
}

.banner_content{
      width: 100%;
      height: 297px;
      border-radius: 2rem;
      position: relative;
      background: var(--dark-100);
      overflow: hidden;
}

.banner_img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
}

.banner_img:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: linear-gradient(-135deg, transparent, var(--dark-100));
}

.banner_img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.banner_data{
      position: relative;
      width: 100%;
      height: 100%;
      padding: 3rem;
}

.banner_data__header{
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
}

.group_online{
      display: flex;
      gap: .5rem;
}

.online_avatar,
.online_avatar img{
      width: 40px;
      height: 40px;
      border-radius: 50%;
}

.online_avatar{
      position: relative;
}

.online_dots{
      width: 12px;
      height: 12px;
      border-radius: 50%;
      position: absolute;
      top: 0;
      right: 0;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red);
}

.group_online span{
      padding: 0 1.5rem;
      background: var(--alpha);
      height: 26px;
      line-height: 26px;
      border-radius: 999px;
      text-transform: uppercase;
      font-weight: 500;
}

.banner_data__menu{
      height: 40px;
      display: flex;
      align-items: center;
      gap: 1rem;
}

.banner_data__menu a{
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      background: var(--alpha);
      border-radius: 50%;
}

.banner_data__menu a *{
      color: var(--light);
      font-size: 2rem;
}

.banner_data__menu a:before{
      content: attr(data-tooltip);
      background: var(--gradient-45);
      color: var(--light);
      text-transform: uppercase;
      top: -2.5rem;
      left: 50%;
      transform: translateX(-50%) scale(0);
      padding: .2rem .5rem;
      border-radius: .3rem;
      transition: .2s;
}

.banner_data__menu a:hover:before{
      transform: translateX(-50%) scale(1);
}

.banner_data__footer{
      margin-top: 11rem;
      display: flex;
      flex-direction: column;
}

.banner_data__footer h2{
      font-size: 2rem;
}

.banner_data__footer span{
      font-size: 1.2rem;
}

.location{
      display: flex;
      height: 22px;
      align-items: flex-end;
      gap: .5rem;
      margin-top: .5rem;
}

.location_icon *{
      font-size: 2rem;
      color: var(--theme);
}

.location span{
      font-size: 1.2rem;
}

.section_content{
      position: absolute;
      width: 100%;
      height: 366px;
      background: var(--light);
      color: var(--dark-200);
      border-radius: 2rem;
      box-shadow: 0 0 30px rgba(0,0,0,.3);
      top: 260px;
      left: 0;
      z-index: 4;
}

.section_description{
      padding: 2rem 3rem 0 3rem;
      height: 130px;
      overflow: hidden;
}

.section_description p{
      font-size: 1.2rem;
}

.section_friends{
      width: 100%;
      margin-top: 1rem;
}

.credits{
      width: 100%;
      height: 70px;
      padding: 1rem;
      border-radius: 2rem;
      background: var(--gradient-45);
      position: absolute;
      left: 0;
      bottom: 0;
      display: flex;
      align-items: flex-end;
      justify-content: center;
}

.credits p{
      font-size: 1.2rem;
}

/** aside **/
.user_avatar{
      width: 100%;
      height: 210px;
      background: var(--gradient);
      border-radius: .5rem;
      padding: .5rem;
}

.user_avatar img{
      width: 200px;
      height: 200px;
      border-radius: .5rem;
}

.aside_column{
      width: 100%;
      height: 390px;
      display: flex;
      position: relative;
      gap: .5rem;
}

.aside_column:before{
      width: .5rem;
      height: 100%;
      background: var(--theme);
      border-radius: 0 0 2rem 2rem;
      top: 0;
      left: 55px;
}

.social{
      width: 55px;
      height: 100%;
      padding-top: 1.5rem;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 1rem;
      flex-shrink: 0;
}

.relation{
      margin-top: 1rem;
      padding-left: 1rem;
}

.relation_title{
      width: 100%;
      height: 34px;
      display: flex;
      align-items: center;
      padding-left: 1rem;
      background: var(--theme);
      border-radius: 0 34px 34px 0;
      position: relative;
      transform: translateX(-1rem);
      gap: .5rem;
}

.relation_title *{
      font-size: 1.6rem;
}

.relation_title h4{
      text-transform: uppercase;
      font-size: 1rem;
}

.relation_status{
      text-transform: uppercase;
      margin: .5rem;
}

.relation_avatar{
      width: 120px;
      height: 120px;
      background: var(--gradient-45);
      border-radius: .5rem;
      padding: .5rem;
}

.relation_avatar img{
      width: 110px;
      height: 110px;
      border-radius: .5rem;
}

.relation h2{
      text-transform: uppercase;
      color: var(--theme);
      font-size: 1.4rem;
      margin: .5rem 0;
}

.relation_about{
      width: 100%;
      height: auto;
      max-height: 140px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--theme);
}

.relation_about p{
      font-size: 1.2rem;
}

.relation a{
      text-transform: uppercase;
      margin-top: 1rem;
      background: var(--gradient-45);
      color: var(--light);
      padding: .4rem 1rem;
      border-radius: 999px;
}

.aside_footer{
      margin-top: 2.8rem;
      display: flex;
      gap: 2rem;
}

.aside_footer button{
      background: var(--gradient-45);
      padding: .75rem 2.5rem;
      border-radius: 999px;
      text-transform: uppercase;
      color: var(--light);
      font-family: var(--ft-1);
}