.video_page{
      width: 100%;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      background: linear-gradient(rgba(0,0,0,.8) 50%, var(--theme) 50%);
      z-index: 1000;
      opacity: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      visibility: visible;
}

.video_page.show{
      animation: show_modal .3s ease-in-out forwards;
}

@keyframes show_modal{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.close_video{
      width: 36px;
      height: 36px;
      position: absolute;
      top: 2rem;
      right: 2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(255,255,255,.1);
      border-radius: 50%;
}

.close_video *{
      font-size: 2rem;
      color: var(--light);
      fill: var(--light);
}

.video_content{
      width: 600px;
      height: 90vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      opacity: 0;
      visibility: hidden;
      transform: scale(0);
      z-index: 2;
}

.video_content.show{
      animation: show_modal_inner .5s .2s ease-in-out forwards;
}

@keyframes show_modal_inner{
      0%{
            opacity: 0;
            visibility: hidden;
            transform: scale(0);
      }
      70%{
            opacity: 1;
            visibility: visible;
            transform: scale(1.4);
      }
      100%{
            opacity: 1;
            visibility: visible;
            transform: scale(1);
      }
}

.video_content h3{
      text-align: center;
      font-size: 1.6rem;
      font-weight: 500;
}

.video_box{
      margin-top: 2rem;
      width: 600px;
      height: 349px;
      border-bottom: solid 1rem var(--theme);
      background: var(--dark-200);
      position: relative;
}

.video_box:before{
      width: 70%;
      height: 50px;
      border-radius: 50%;
      left: 15%;
      bottom: -25px;
      filter: blur(25px);
      opacity: .4;
      background: #000;
      z-index: -1;
}

.video_box iframe{
      border: none;
}