.gallery{
      position: fixed;
      width: 100%;
      height: 100vh;
      min-height: 1080px;
      background: rgba(0,0,0,.7);
      z-index: 100;
      top: 0;
      left: 0;
      display: flex;
      justify-content: center;
      opacity: 0;
      visibility: hidden;
}

.gallery.show{
      animation: show_gallery .2s ease-in-out forwards;
}

@keyframes show_gallery{
      0%{
            opacity: 0;
            visibility: hidden;
      }
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.close_gallery,
.btn_gallery__zoom{
      position: absolute;
      bottom: 2rem;
      right: 2rem;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(255,255,255,.1);
}

.btn_gallery__zoom{
      background: rgba(0,0,0,.4);
}

.close_gallery *,
.btn_gallery__zoom *{
      color: var(--light);
      font-size: 2rem;
}

.wrapper_gallery{
      width: 90%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.wrapper_gallery h2{
      font-size: 5rem;
      color: var(--light);
      margin: 4rem 0;
      font-family: var(--ft-4);
      font-weight: 400;
}

.group_gallery__img{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 1rem;
}

.group_gallery__img.empty{
      grid-template-columns: repeat(1, 1fr);
}

.gallery_img_card{
      position: relative;
      width: 100%;
      height: 225px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .6rem;
      overflow: hidden;
      background: var(--dark);
}

.gallery_img_card img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      opacity: 1;
}

.btn_gallery__zoom{
      width: 50px;
      height: 50px;
      transition: .2s;
}

.btn_gallery__zoom:hover{
      transform: scale(1.3);
}