@import url('https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&family=Fira+Mono:wght@400;500;700&family=Poppins:wght@200;300;400;500;600&family=Xanh+Mono&display=swap');

:root{
  /** fonts */
  --ft-1: 'Poppins', sans-serif;
  --ft-2: 'Courier Prime', monospace;
  --ft-3: 'Fira Mono', monospace;
  --ft-4: 'Xanh Mono', monospace;

  /** colors */
  --green: #45d985;
  --light: #fff;
  --red: #d90000;
  --alpha: rgba(0,0,0,.3);

  /** default theme */
  --theme: #e1395d;
  --second: #f2684b;
  --dark-200: #1a2129;
  --dark-100: #212934;
  --gradient-90: linear-gradient(90deg, var(--second), var(--theme));
  --gradient-45: linear-gradient(45deg, var(--second), var(--theme));
  --gradient: linear-gradient(var(--second), var(--theme));

  /** theme2 */
  --theme: #ff4ba7;
  --seconde: #ffda64;

  /** theme 3*/
  --theme: #2f80ff;
  --second: #3ccbff;

  /** theme 4 */
  --theme: #5cb270;
  --second: #f4f269;

  /** theme 5 */
  --theme: #2d00f7;
  --second: #f20089;
  
  /** globals */
  font-family: var(--ft-1);
  font-size: 62.5%;
  color: var(--light);

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: auto;
  min-height: 100vh;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--dark-200);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

.bg_grad{
  display: inline-block;
  color: transparent;
  background: var(--gradient-45);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--dark-200);
  border-radius: 999px;
}

::-webkit-scrollbar-thumb{
  background: var(--theme);
  border-radius: 999px;
}