.empty_content{
      width: 100%;
      height: 100%;
      min-height: 120px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 1.5rem;
}

.empty_content *{
      font-size: 6rem;
      color: rgba(0,0,0,.4);
}

.empty_content h3{
      font-size: 1.4rem;
      color: rgba(0,0,0,.3);
}