.btn_social{
      width: 38px;
      height: 38px;
      border-radius: 50%;
      background: var(--gradient-45);
      padding: .2rem;
}

.btn_social span{
      width: 34px;
      height: 34px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background: var(--dark-200);
      color: var(--light);
}

.btn_social span *{
      font-size: 1.6rem;
      transition: .2s;
}

.btn_social span:hover *{
      color: var(--theme);
}